@media screen and (min-width: 961px) {
    .contact-outer-div {
        position: relative;
        width: 100%;
        height: 500px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-top: 1px solid rgb(230, 230, 230);
    }

    .contact-inner-div {
        min-width: 1200px;
        max-width: 1600px;
        height: 80%;
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .contact-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1em;
    }

    .contact-header {
        font-weight: 300;
        line-height: 1.3;
        font-size: 35px;
    }

    .contact-info-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .contact-info-div {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 280px;
        margin: 30px 0;
    }

    .contact-icon {
        font-size: 1.5em;
    }

    .contact, .contact-resume {
        margin-top: 20px;
        font-size: 18px;
        font-weight: 400;
        color: #A649B3;
    }

    .contact-resume {
        text-decoration: underline;
    }

    .contact-links-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 2em;
        margin: 30px 0;
    }

}


